import axios from "axios";
import * as Config from '../../constant/config'
import * as Status from '../../constant/STATUS_CODE'


export default function callApi(endpoint,method='GET',body){
    return axios({
        method:method,
        url:`${Config.API_ENDPOINT_SERVER}/${endpoint}`,
        data:body
    }).catch(err=>{
        console.log()
    })
}